.header-top {
	background-color: #0899d0;
	height: 58px;
	line-height: 58px;
}

.logo-img {
	font-size: 20px;
}

.header-top .top {
	font-size: 11px;
}

.top .custom-color {
	color: #017cab;
}

.top .icon-bell {
    font-size: 23px;
    position: relative;
}
.border-icon
{
    position: relative;

}
.border-icon ::after
{
    content: "";
    height: 50%;
    width: 1px;
    background: #1cade4;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -12px;

}
.border-icon::before
{
    background:#1cade4;
    position: absolute;
    content: "";
    width: 1px;
    height: 50%;
    top: 50%;
    transform: translateY(-50%);
    left: -12px;

}
.top li  {
	margin-right: 35px;
}
.top li a {
	text-decoration:none;
}


.no-style-list {
	list-style: none;
}

.nav-item .nav-link {
	color: #fff;
	text-decoration: none;
	text-transform: uppercase;
	padding: 0px;
}
.header-top .dropdown-menu .dropdown-item {
  
    padding: 10px;
    line-height:20px;
 
}
.header-top .dropdown-menu
{
    margin: 0px;
    padding:0px;
}
.header-top  .dropdown-item:after
{
    position: absolute;
    top: -9px;
    right: 1px;
    display: inline-block;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #ffffff;
    border-left: 10px solid transparent;
    content: '';

}
.header-top  .dropdown-item:before {
    position: absolute;
    top: -8px;
    right: 1px;

    display: inline-block;
    border-right: 7px solid transparent;
    border-bottom: 7px solid #ffffff;
    border-left: 7px solid transparent;
    border-bottom-color: rgba(169, 161, 161, 0.2);
    content: '';
   
  }

  .header-top.dropdown-menu
  {
      line-height:30px;

  }
 
 

    /*header-inner*/


.header-inner {
	background-color: #1cade4;
}

.header-inner .workplace {
	display: inline-block;
	background-color: #0a8fb9;
	color: #fff;
	font-weight: 700;
	font-size: 13px;
	border-radius: 0 23px 23px 0;
	line-height: 50px;
	height: 100%;
	width: 180px;
	max-width: 180px;
	text-transform: uppercase;
	text-align: center;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.header-inner a.selected {
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	color: #0899d0;
	background-color: #fafafa;
	font-weight: bolder;
	padding: 18px 38px;
	font-size: 13px;
}
.header-inner a {
text-decoration:none;
}

