    display: block;
    margin-bottom: 11px;
    font-weight: 700;
}
.tabs-react li
{
    text-transform: uppercase;
        text-align: center;
        display: inline-block;
        cursor: pointer;
        color: #414141;
        font-size: 14px;
        padding: 0 20px;
        text-decoration: none;
        position: relative;
    

}
.tabs-react li
{
   font-weight:bold;
}
.tabs-react li::after {
    content: " ";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #1cade4;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    transition: -webkit-transform .15s ease-out;
    transition: transform .15s ease-out;
    transition: transform .15s ease-out, -webkit-transform .15s ease-out;
    transition: transform .15s ease-out,-webkit-transform .15s ease-out;
}

.tabs-react__tab-list li.tabs-react:after, .tabs-react li.selected:after, .tabs-react li:hover:after {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}
