.datasource h1{
    color: #414141;
    font-size: 24px;
    font-weight: 500;


}
.datasource
{
    margin-top:25px;
}

.definition-list   a:active, a:focus, a:hover, a:visited {
    color: #1cade4;
    text-decoration: none;
   
}
ul.definition-list {
    display: flex;
    text-transform: uppercase;
    font-weight: 700;
}
.no-style-list {
	list-style: none;
}
ul.definition-list>li {
    text-align: left;
    display: flex;
    flex-direction: column;
    flex-basis: auto;
    flex-shrink: 1;
    margin-right: 25px;
    overflow-x: hidden;
}
ul.definition-list>li>strong {
    font-size: 11px;
    color: #414141;
    text-overflow: ellipsis;
    font-weight:500;
}
ul.definition-list>li>span {
    overflow-x: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 18px;
    color: #1cade4;
    margin-top: 8px;
}
.button-group {
    display: inline-flex;
    overflow: visible!important;
}
hr {
    height: 1px;
    border: 0;
    background: #ededed;
    margin: 5px;
}
.tabs-react {
    line-height: 62px;
    height: 63px;
    text-align: left;
    border-bottom: 1px solid #ededed;
    display: block;
    margin-bottom: 11px;
    font-weight: 700;
}
.tabs-react li
{
    text-transform: uppercase;
        text-align: center;
        display: inline-block;
        cursor: pointer;
        color: #414141;
        font-size: 14px;
        padding: 0 20px;
        text-decoration: none;
        position: relative;
    

}
.tabs-react li
{
   font-weight:bold;
}
.tabs-react li::after {
    content: " ";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #1cade4;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    transition: -webkit-transform .15s ease-out;
    transition: transform .15s ease-out;
    transition: transform .15s ease-out,-webkit-transform .15s ease-out;
}

.tabs-react__tab-list li.tabs-react:after, .tabs-react li.selected:after, .tabs-react li:hover:after {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}
.definition-list .dropdown
{
    border: 1px solid #1cade4;
    -webkit-appearance: none;
       background-color: #1cade4;
       border-radius: 25px;
}
.definition-list .dropdown a
{
   color:#fff;
}
.definition-list .dropdown-menu
{
    margin-top:15px;
    padding:0px;
}
.definition-list .dropdown-item
{
    font-weight:500;
    padding: 10px 15px;
   

}
.definition-list .dropdown-item:after
{
    position: absolute;
    top: -12px;
    right: 1px;
    display: inline-block;
    border-right: 12px solid transparent;
    border-bottom: 12px solid #f1f1f1;
    border-left: 12px solid transparent;
    content: '';

}
.definition-list .dropdown-item:before {
    position: absolute;
    top: -8px;
    right: 3px;

    display: inline-block;
    border-right: 7px solid transparent;
    border-bottom: 7px solid #ffffff;
    border-left: 7px solid transparent;
    border-bottom-color: rgba(169, 161, 161, 0.2);
    content: '';
   
  }
  .definition-list .dropdown-toggle::after {
    display: inline-block;
    margin-left: 15px;
  
}
  